* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;

  @media (max-width: 43.75em) {
    font-size: 50%;
  }
}

body {
  height: 130rem;
  font-size: 1.6rem;
  background-color: $color-dark-blue;
  font-family: 'Roboto Condensed', Helvetica, Arial;
}

.container {
  max-width: 115rem;
  margin: 0 auto;
}

.app__wrapper {
  min-width: 45rem;
  padding: 0 5rem;
}
